/**
 * Comprehensive date utility functions for the application
 * Ensures consistent date handling across all components
 */

export function getCurrentDate(): string {
  return new Date().toISOString().split("T")[0];
}

export function getCurrentDateTime(): string {
  return new Date().toISOString();
}

export function getCurrentDateTimeLocal(): string {
  const now = new Date();
  const offset = now.getTimezoneOffset();
  const localTime = new Date(now.getTime() - offset * 60 * 1000);
  return localTime.toISOString().slice(0, 16);
}

export function getCurrentTime(): string {
  const now = new Date();
  return now.toTimeString().slice(0, 5); // HH:MM format
}

export function formatDateForDisplay(
  dateString: string | undefined | null
): string {
  if (!dateString) return "N/A";
  try {
    return new Date(dateString).toLocaleDateString("en-US", {
      year: "numeric",
      month: "short",
      day: "numeric",
    });
  } catch {
    return "Invalid Date";
  }
}

export function formatDateTimeForDisplay(
  dateString: string | undefined | null
): string {
  if (!dateString) return "N/A";
  try {
    return new Date(dateString).toLocaleString("en-US", {
      year: "numeric",
      month: "short",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    });
  } catch {
    return "Invalid Date";
  }
}

export function isToday(dateString: string | undefined | Date): boolean {
  if (!dateString) return false;
  return new Date(dateString).toDateString() === new Date().toDateString();
}

export function isYesterday(dateString: string | undefined | null): boolean {
  if (!dateString) return false;
  try {
    const yesterday = new Date();
    yesterday.setDate(yesterday.getDate() - 1);
    const checkDate = dateString.split("T")[0];
    return yesterday.toISOString().split("T")[0] === checkDate;
  } catch {
    return false;
  }
}

export function isTomorrow(dateString: string | undefined | null): boolean {
  if (!dateString) return false;
  try {
    const tomorrow = new Date();
    tomorrow.setDate(tomorrow.getDate() + 1);
    const checkDate = dateString.split("T")[0];
    return tomorrow.toISOString().split("T")[0] === checkDate;
  } catch {
    return false;
  }
}

export function isCurrentWeek(dateString: string | undefined | null): boolean {
  if (!dateString) return false;
  try {
    const today = new Date();
    const checkDate = new Date(dateString);
    const startOfWeek = new Date(
      today.setDate(today.getDate() - today.getDay())
    );
    const endOfWeek = new Date(
      today.setDate(today.getDate() - today.getDay() + 6)
    );

    return checkDate >= startOfWeek && checkDate <= endOfWeek;
  } catch {
    return false;
  }
}

export function isCurrentMonth(dateString: string | undefined | null): boolean {
  if (!dateString) return false;
  try {
    const today = new Date();
    const checkDate = new Date(dateString);
    return (
      today.getMonth() === checkDate.getMonth() &&
      today.getFullYear() === checkDate.getFullYear()
    );
  } catch {
    return false;
  }
}

export function getDateRange(days: number): { start: string; end: string } {
  const end = new Date();
  const start = new Date();
  start.setDate(start.getDate() - days);

  return {
    start: start.toISOString().split("T")[0],
    end: end.toISOString().split("T")[0],
  };
}

export function addDays(dateString: string, days: number): string {
  const date = new Date(dateString);
  date.setDate(date.getDate() + days);
  return date.toISOString().split("T")[0];
}

export function subtractDays(dateString: string, days: number): string {
  const date = new Date(dateString);
  date.setDate(date.getDate() - days);
  return date.toISOString().split("T")[0];
}

export function getWeekStart(dateString?: string): string {
  const date = dateString ? new Date(dateString) : new Date();
  const day = date.getDay();
  const diff = date.getDate() - day;
  const weekStart = new Date(date.setDate(diff));
  return weekStart.toISOString().split("T")[0];
}

export function getWeekEnd(dateString?: string): string {
  const date = dateString ? new Date(dateString) : new Date();
  const day = date.getDay();
  const diff = date.getDate() - day + 6;
  const weekEnd = new Date(date.setDate(diff));
  return weekEnd.toISOString().split("T")[0];
}

export function getMonthStart(dateString?: string): string {
  const date = dateString ? new Date(dateString) : new Date();
  return new Date(date.getFullYear(), date.getMonth(), 1)
    .toISOString()
    .split("T")[0];
}

export function getMonthEnd(dateString?: string): string {
  const date = dateString ? new Date(dateString) : new Date();
  return new Date(date.getFullYear(), date.getMonth() + 1, 0)
    .toISOString()
    .split("T")[0];
}

export function getRelativeDateLabel(
  dateString: string | undefined | null
): string {
  if (!dateString) return "No Date";

  try {
    const today = getCurrentDate();
    const yesterday = subtractDays(today, 1);
    const tomorrow = addDays(today, 1);

    if (dateString === today) return "Today";
    if (dateString === yesterday) return "Yesterday";
    if (dateString === tomorrow) return "Tomorrow";

    return formatDateForDisplay(dateString);
  } catch {
    return "Invalid Date";
  }
}

export function getDaysDifference(date1: string, date2: string): number {
  try {
    const d1 = new Date(date1);
    const d2 = new Date(date2);
    const diffTime = Math.abs(d2.getTime() - d1.getTime());
    return Math.ceil(diffTime / (1000 * 60 * 60 * 24));
  } catch {
    return 0;
  }
}

export function isOverdue(dateString: string | undefined | null): boolean {
  if (!dateString) return false;
  try {
    const today = getCurrentDate();
    return dateString < today;
  } catch {
    return false;
  }
}

export function isDueSoon(
  dateString: string | undefined | null,
  daysThreshold = 3
): boolean {
  if (!dateString) return false;
  try {
    const today = getCurrentDate();
    const daysUntilDue = getDaysDifference(today, dateString);
    return daysUntilDue <= daysThreshold && dateString >= today;
  } catch {
    return false;
  }
}

export function getTimeUntilDeadline(
  dateString: string | undefined | null
): string {
  if (!dateString) return "No Deadline";

  try {
    const now = new Date();
    const deadline = new Date(dateString);
    const diff = deadline.getTime() - now.getTime();

    if (diff < 0) return "Overdue";

    const days = Math.floor(diff / (1000 * 60 * 60 * 24));
    const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));

    if (days > 0) return `${days}d ${hours}h`;
    if (hours > 0) return `${hours}h ${minutes}m`;
    return `${minutes}m`;
  } catch {
    return "Invalid Deadline";
  }
}

export function refreshCurrentDate(): string {
  // Force refresh of current date - useful for real-time updates
  return new Date().toISOString().split("T")[0];
}

export function refreshCurrentDateTime(): string {
  // Force refresh of current datetime - useful for real-time updates
  return new Date().toISOString();
}

export function formatDateShort(dateString: string | undefined | null): string {
  if (!dateString) return "N/A";
  try {
    const date = new Date(dateString);
    return date.toLocaleDateString("en-US", {
      month: "short",
      day: "numeric",
      year: "numeric",
    });
  } catch {
    return "Invalid Date";
  }
}

export function getDateFromToday(days: number): string {
  const date = new Date();
  date.setDate(date.getDate() + days);
  return date.toISOString().split("T")[0];
}

export function isPastDate(dateString: string | undefined | null): boolean {
  if (!dateString) return false;
  try {
    const date = new Date(dateString);
    const today = new Date();
    today.setHours(0, 0, 0, 0);
    return date < today;
  } catch {
    return false;
  }
}

export function isFutureDate(dateString: string | undefined | null): boolean {
  if (!dateString) return false;
  try {
    const date = new Date(dateString);
    const today = new Date();
    today.setHours(23, 59, 59, 999);
    return date > today;
  } catch {
    return false;
  }
}

export function getDaysBetween(startDate: string, endDate: string): number {
  try {
    const start = new Date(startDate);
    const end = new Date(endDate);
    const diffTime = Math.abs(end.getTime() - start.getTime());
    return Math.ceil(diffTime / (1000 * 60 * 60 * 24));
  } catch {
    return 0;
  }
}

export function getCurrentTimestamp(): string {
  return new Date().toISOString();
}

export function formatTimeForDisplay(
  timeString: string | undefined | null
): string {
  if (!timeString) return "N/A";
  try {
    const [hours, minutes] = timeString.split(":");
    const hour = Number.parseInt(hours);
    const ampm = hour >= 12 ? "PM" : "AM";
    const displayHour = hour % 12 || 12;
    return `${displayHour}:${minutes} ${ampm}`;
  } catch {
    return "Invalid Time";
  }
}

export function getWeekStartDate(): string {
  const today = new Date();
  const day = today.getDay();
  const diff = today.getDate() - day + (day === 0 ? -6 : 1); // Adjust when day is Sunday
  const monday = new Date(today.setDate(diff));
  return monday.toISOString().split("T")[0];
}

export function getMonthStartDate(): string {
  const today = new Date();
  return new Date(today.getFullYear(), today.getMonth(), 1)
    .toISOString()
    .split("T")[0];
}

export function getYearStartDate(): string {
  const today = new Date();
  return new Date(today.getFullYear(), 0, 1).toISOString().split("T")[0];
}
